import './App.css';
import { useState } from "react";
import { AiFillEdit } from "react-icons/ai";
import * as XLSX from "xlsx";

const NUMBER_OF_COLUMNS = 6;
const HEADERS = [
  "Date",
  "Name",
  "Age/Sex",
  "Chief Complaint",
  "Diagnosis",
  "Disposition"
]

function PatientInfo({patientInfo, setPatientInfo}) {
  return (
    <>
      <div>
        <label>
          Patient Information
        </label>
      </div>
      <div>
        <textarea
          rows={NUMBER_OF_COLUMNS}
          cols={50}
          value={patientInfo}
          onChange={e => setPatientInfo(e.target.value)}
        />
      </div>
    </>
  );
}

export default function App() {
  const now = new Date();
  const [patientInfo, setPatientInfo] = useState(now.toLocaleDateString());
  const [patientList, setPatientList] = useState([]);
  const [editState, setEditState] = useState(false);
  const [editPatientIndex, setEditPatientIndex] = useState(0);

  function handleEditButton(info, index) {
    const patientListCopy = [...patientList];
    patientListCopy.splice(index, 1);

    setPatientInfo(info.trim());
    setPatientList(patientListCopy);
    setEditPatientIndex(index);
    setEditState(true);
  }

  function handleAddPatient() {
    let patientInfoCopy = patientInfo;

    const lines = patientInfoCopy.trim().split("\n");
    for(let i = lines.length; i < NUMBER_OF_COLUMNS; i++) {
      patientInfoCopy += "\n";
    }

    if(editState) {
      const patientListCopy = [...patientList];
      patientListCopy.splice(editPatientIndex, 0, patientInfoCopy);
      setPatientList(patientListCopy);
    } else {
      setPatientList([...patientList, patientInfoCopy]);
    }

    setPatientInfo(now.toLocaleDateString());
    setEditState(false);
  }

  function handleSaveTable() {
    const data = patientList.map((info, index) => {
      let dict = {};
      const lines = info.split("\n");

      for(let i = 0; i < NUMBER_OF_COLUMNS; i++) {
        dict[HEADERS.at(i)] = lines.at(i);
      }

      return dict;
    });

    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Patient Information");

    XLSX.writeFile(workbook, "PatientInformation.xlsx", {
      compression: true
    });
  }

  const headers = HEADERS.map((header, index) =>
    <th key={index}>{header}</th>
  );

  const patientListColumns = patientList.map((info, index) => {
    const lines = info.split("\n");

    const columns = lines.map((line, index) =>
      <td key={index}>{line}</td>
    );

    return (
      <tr key={index}>
        {columns}
        <td>
          <button onClick={() => handleEditButton(info, index)}>
            <AiFillEdit />
          </button>
        </td>
      </tr>
    );
  });

  return (
    <>
      <PatientInfo patientInfo={patientInfo} setPatientInfo={setPatientInfo}/>
      <button onClick={() => handleAddPatient()}>Add Patient</button>
      <table>
        <thead>
          <tr>
            {headers}
          </tr>
        </thead>
        <tbody>
          {patientListColumns}
        </tbody>
      </table>
      <button onClick={() => handleSaveTable()}>Save Table</button>
    </>
  );
}

